@import url("https://fonts.googleapis.com/css2?family=Space+Mono&display=swap");
* {
  padding: 0;
  margin: 0;
}

body {
  background: repeat url(./background.jpg);
  font-size: 0.66rem;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

p {
  font-size: 0.8rem;
}

a {
  color: #ed613f;
  text-decoration: none;
  font-weight: normal !important;
}

nav {
  max-width: 70rem;
  background-color: #065f7e;
  padding: 0 0 1rem 2rem;
}
nav .burger {
  display: none;
}
nav ul {
  padding: 2rem 1.5rem 0 0;
  display: flex;
  list-style: none;
}
nav ul li a {
  background-color: #ed613f;
  color: #f4f0bf;
  text-align: center;
  font-weight: bolder;
  max-width: 8rem;
  padding: 1rem 2rem 1rem 1.5rem;
}
nav ul li a:hover {
  background-color: #ed613f;
  color: white;
  text-align: center;
  font-weight: bolder;
  box-shadow: 0 4px #5900ff;
  text-decoration: none;
  padding: 1rem 2rem 1rem 1.5rem;
}

header {
  max-width: 70rem;
  background-color: #ffc000;
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  background-position: 0% 12%;
  background-size: 100%;
  padding: 3rem 1.5rem 4rem 1.5rem;
}
header section {
  padding-left: 2rem;
  text-align: center;
}
header section h3 {
  color: #065f7e;
  padding-bottom: 1rem;
}
header section p {
  font-family: "Space Mono", monospace;
  color: #040050;
  line-height: 1.8;
  font-size: 0.9rem;
}
header img {
  padding: 0;
  margin: 0;
  max-width: 80%;
  border: 10px solid #ed613f;
}

