.medallas {
  padding: 1rem 0;
}
.medallas input {
  display: none;
}
.medallas label {
  display: contents;
}
.medallas label img {
  margin-left: 0.1rem;
  max-width: 12%;
}

.toDoList {
  background-color: #212529;
  padding: 1rem 0;
}
.toDoList label {
  margin-left: 1rem;
}
.toDoList .toDoNew {
  margin-left: 1rem;
}

.contadores {
  display: flex;
  gap: 3rem;
}
.contadores .tickets {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  padding: 1rem 0 0 0;
}

.apiContainer {
  max-width: 70rem;
  background-color: #ffc000;
  padding: 0 2rem 2rem 2rem;
}
.apiContainer .batalla {
  background: no-repeat url(stadium.png);
  background-position: center center;
  background-size: 20%;
  display: flex;
  gap: 1rem;
  justify-content: space-around;
  border: 8px solid white;
  min-height: 27.4rem;
}
.apiContainer .batalla h3 {
  padding-bottom: 0.5rem;
  font-size: 1.2rem;
}
.apiContainer .batalla p {
  font-size: 0.6rem;
  line-height: 1rem;
}
.apiContainer .batalla img {
  width: 15rem;
}

.card {
  text-transform: capitalize;
}

.titleColor {
  background-color: #000000 !important;
  color: white;
}

.agregarTodo {
  padding-top: 0.5rem;
}

.m-auto {
  margin: 0 auto;
}

.reglamentoContainer {
  max-width: 70rem;
  background-color: #ffc000;
  padding: 1.5rem 2rem 3rem 2rem;
}
.reglamentoContainer .infoPDF {
  border: 8px solid #bf593e;
  background-color: #f4f3c3;
  display: flex;
  gap: 1rem;
  align-items: baseline;
  padding: 1rem 1rem 0.5rem 1rem;
  justify-content: space-between;
}
.reglamentoContainer .reglamento {
  background: #bf593e;
  text-align: -webkit-center;
  padding: 1rem 0 1rem 0;
}

footer {
  background-color: #303030;
  padding-bottom: 2rem;
}
footer section {
  padding: 3rem 2rem 1rem 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 70rem;
  align-items: center;
}
footer section .icons h4 {
  color: white;
  font-weight: normal;
}
footer section .icons a {
  padding-right: 0.8rem;
  font-size: 1.5rem;
  opacity: 0.6;
}
footer section .icons a:hover {
  opacity: 1;
}
footer section .icons a i {
  font-size: 1.5srem;
}
footer section .textfooter {
  text-align: center;
}
footer section .textfooter p {
  color: white;
  font-size: 0.6rem;
}
footer div {
  padding: 0 2rem 0 2rem;
}
footer div hr {
  max-width: 70rem;
  margin: 0 auto;
}

/* nav-icon-5 */
.nav-icon-5 {
  width: 35px;
  height: 30px;
  margin: 10px 10px;
  position: relative;
  display: inline-block;
}

.nav-icon-5 span {
  background-color: #f4f0bf;
  position: absolute;
  border-radius: 2px;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 4px;
  transition-duration: 500ms;
}

.nav-icon-5 span:nth-child(1) {
  top: 0px;
  left: 0px;
}

.nav-icon-5 span:nth-child(2) {
  top: 13px;
  left: 0px;
  opacity: 1;
}

.nav-icon-5 span:nth-child(3) {
  bottom: 0px;
  left: 0px;
}

.nav-icon-5:not(.open):hover span:nth-child(1) {
  transform: rotate(-3deg) scaleY(1.1);
}

.nav-icon-5:not(.open):hover span:nth-child(2) {
  transform: rotate(3deg) scaleY(1.1);
}

.nav-icon-5:not(.open):hover span:nth-child(3) {
  transform: rotate(-4deg) scaleY(1.1);
}

.nav-icon-5.open span:nth-child(1) {
  transform: rotate(45deg);
  top: 13px;
}

.nav-icon-5.open span:nth-child(2) {
  opacity: 0;
}

.nav-icon-5.open span:nth-child(3) {
  transform: rotate(-45deg);
  top: 13px;
}

@media screen and (max-width: 960px) {
  .links {
    position: absolute;
    top: -700px;
    left: -2000px;
    margin-left: auto;
    margin-right: auto;
    transition: all 0.5s ease;
    text-align-last: start;
  }
  .links a {
    display: block;
    background-color: #065f7e;
    color: #065f7e;
  }

  .active {
    width: 100%;
    display: block;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 1%;
    left: 0;
  }
  .active li a {
    font-size: 2rem;
    margin-top: 1rem;
    color: white;
    background-color: #065f7e;
    max-width: 25rem;
  }

  .bigNav {
    min-height: 30rem;
  }
}
@media screen and (max-width: 960px) {
  header {
    max-width: 70rem;
    background-color: #ffc000;
    display: inline-block;
    text-align: center;
    padding: 2rem 1.5rem 1rem 1.5rem;
  }

  .fourPlayers {
    display: block;
  }
  .fourPlayers .nes-container {
    margin-top: 1rem;
  }

  .batalla {
    display: grid !important;
    background-image: none !important;
    gap: 1rem;
  }

  nav {
    display: flex;
    justify-content: flex-end;
  }
  nav .burger {
    padding: 1rem 1rem 0 0;
    display: block;
  }

  .reglamentoContainer .reglamento {
    background: #bf593e;
    text-align: -webkit-center;
    padding: 1rem 0 1rem 0;
    overflow: scroll;
  }
}

