@import "components/mixins", "components/vars";
@import url("https://fonts.googleapis.com/css2?family=Space+Mono&display=swap");

* {
  @include reset-pm();
}

body {
  background: repeat url(./background.jpg);
  font-size: 0.66rem;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

p {
  font-size: 0.8rem;
}

a {
  color: #ed613f;
  text-decoration: none;
  font-weight: normal !important;
}

nav {
  max-width: 70rem;
  background-color: #065f7e;
  padding: 0 0 1rem 2rem;

  .burger {
    display: none;
  }

  ul {
    padding: 2rem 1.5rem 0 0;
    display: flex;
    //gap: 3rem;
    list-style: none;
    li {
      a {
        @include btn($bg-color, $font-color);
        max-width: 8rem;
        padding: 1rem 2rem 1rem 1.5rem;
        &:hover {
          @include btn($bg-color, white);
          box-shadow: 0 4px #5900ff;
          text-decoration: none;
          padding: 1rem 2rem 1rem 1.5rem;
        }
      }
    }
  }
}

header {
  @include container;
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  background-position: 0% 12%;
  background-size: 100%;
  padding: 3rem 1.5rem 4rem 1.5rem;
  section {
    padding-left: 2rem;
    text-align: center;
    h3 {
      color: #065f7e;
      padding-bottom: 1rem;
    }
    p {
      font-family: "Space Mono", monospace;
      color: #040050;
      line-height: 1.8;
      font-size: 0.9rem;
    }
  }
  img {
    @include reset-pm();
    max-width: 80%;
    border: 10px solid #ed613f;
  }
}

.fourPlayers {
  @include contPadding;
  @include container;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
  .playerOne {
    @include playersColor($primary-color, $primary-colorb);
  }
  .playerTwo {
    @include playersColor($secondary-color, $secondary-colorb);
  }
  .playerThree {
    @include playersColor($terciary-color, $terciary-colorb);
  }
  .playerFour {
    @include playersColor($four-color, $four-colorb);
  }
}

.medallas {
  padding: 1rem 0;
  input {
    display: none;
  }
  label {
    display: contents;
    img {
      margin-left: 0.1rem;
      max-width: 12%;
    }
  }
}

.toDoList {
  background-color: #212529;
  padding: 1rem 0;
  label {
    margin-left: 1rem;
  }
  .toDoNew {
    margin-left: 1rem;
  }
}

.contadores {
  display: flex;
  gap: 3rem;
  .tickets {
    display: flex;
    align-items: baseline;
    gap: 0.5rem;
    padding: 1rem 0 0 0;
  }
}

.apiContainer {
  @include container;
  padding: 0 2rem 2rem 2rem;
  .batalla {
    background: no-repeat url(stadium.png);
    background-position: center center;
    background-size: 20%;
    display: flex;
    gap: 1rem;
    justify-content: space-around;
    border: 8px solid white;
    min-height: 27.4rem;
    h3 {
      padding-bottom: 0.5rem;
      font-size: 1.2rem;
    }
    p {
      font-size: 0.6rem;
      line-height: 1rem;
    }
    img {
      width: 15rem;
    }
  }
}

.card {
  text-transform: capitalize;
}

.titleColor {
  background-color: #000000 !important;
  color: white;
}

.agregarTodo {
  padding-top: 0.5rem;
}

.m-auto {
  margin: 0 auto;
}

.reglamentoContainer {
  @include container;
  padding: 1.5rem 2rem 3rem 2rem;
  .infoPDF {
    border: 8px solid #bf593e;
    background-color: #f4f3c3;
    display: flex;
    gap: 1rem;
    align-items: baseline;
    padding: 1rem 1rem 0.5rem 1rem;
    justify-content: space-between;
  }
  .reglamento {
    background: #bf593e;
    text-align: -webkit-center;
    padding: 1rem 0 1rem 0;
  }
}

footer {
  background-color: #303030;
  padding-bottom: 2rem;
  section {
    padding: 3rem 2rem 1rem 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    max-width: 70rem;
    align-items: center;
    .icons {
      h4{
        color: white;
        font-weight: normal;
      }
      a {
        padding-right: 0.8rem;
        font-size: 1.5rem;
        opacity: 0.6;
        &:hover {
          opacity: 1;
        }
        i {
          font-size: 1.5srem;
        }
      }
    }
    .textfooter {
      text-align: center;
      p {
        color: white;
        font-size: 0.6rem;
      }
    }
  }
  div{
    padding: 0 2rem 0 2rem;
    hr {
      max-width: 70rem;
      margin: 0 auto;
    }
  }
 
}

/* nav-icon-5 */
.nav-icon-5 {
  width: 35px;
  height: 30px;
  margin: 10px 10px;
  position: relative;
  display: inline-block;
}
.nav-icon-5 span {
  background-color: $font-color;
  position: absolute;
  border-radius: 2px;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  width: 100%;
  height: 4px;
  transition-duration: 500ms;
}
.nav-icon-5 span:nth-child(1) {
  top: 0px;
  left: 0px;
}
.nav-icon-5 span:nth-child(2) {
  top: 13px;
  left: 0px;
  opacity: 1;
}
.nav-icon-5 span:nth-child(3) {
  bottom: 0px;
  left: 0px;
}
.nav-icon-5:not(.open):hover span:nth-child(1) {
  transform: rotate(-3deg) scaleY(1.1);
}
.nav-icon-5:not(.open):hover span:nth-child(2) {
  transform: rotate(3deg) scaleY(1.1);
}
.nav-icon-5:not(.open):hover span:nth-child(3) {
  transform: rotate(-4deg) scaleY(1.1);
}
.nav-icon-5.open span:nth-child(1) {
  transform: rotate(45deg);
  top: 13px;
}
.nav-icon-5.open span:nth-child(2) {
  opacity: 0;
}
.nav-icon-5.open span:nth-child(3) {
  transform: rotate(-45deg);
  top: 13px;
}

@media screen and(max-width: map-get($breakpoints, "grande")) {
  .links {
    position: absolute;
    top: -700px;
    left: -2000px;
    margin-left: auto;
    margin-right: auto;
    transition: all 0.5s ease;
    text-align-last: start;
    a {
      display: block;
      background-color: #065f7e;
      color: #065f7e;
    }
  }
  .active {
    width: 100%;
    display: block;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 1%;
    left: 0;
    li{
      a {
        font-size: 2rem;
        margin-top: 1rem;
        color: white;
        background-color: #065f7e;
        max-width: 25rem;
      }
    }
  }
  .bigNav{
    min-height: 30rem;
  }
}

@media screen and(max-width: map-get($breakpoints, "grande")) {
  header {
    @include container;
    display: inline-block;
    text-align: center;
    padding: 2rem 1.5rem 1rem 1.5rem;
  }

  .fourPlayers {
    display: block;
    .nes-container {
      margin-top: 1rem;
    }
  }

  .batalla {
    display: grid !important;
    background-image: none !important;
    gap: 1rem;
  }

  nav {
    display: flex;
    justify-content: flex-end;
    .burger {
      padding: 1rem 1rem 0 0;
      display: block;
    }
  }
  .reglamentoContainer {
    .reglamento {
      background: #bf593e;
      text-align: -webkit-center;
      padding: 1rem 0 1rem 0;
      overflow: scroll;
    }
  }

}
